.mail {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  position: fixed;
  right: 35px;
  bottom: 0px;
  width: 19px;
  z-index: 10;
  justify-content: center;
  align-items: center;

  &__text {
    display: flex;
    font-size: 12px;
    writing-mode: vertical-rl;
    color: #a8b2d1;
    letter-spacing: 1.2px;
    cursor: pointer;
    transition: all 0.5s ease;
    &:hover {
      color: $-c-green;
    }
    &:after {
      content: "";
      display: block;
      margin: 0px auto;
      margin-top: 25px;
      width: 1px;
      height: 90px;
      background-color: #a8b2d1;
      transition: none;
    }
  }
}
@media (max-width: 1080px) {
  .mail {
    right: 15px;
  }
}
@media (max-width: 768px) {
  .mail {
    display: none;
  }
}
